/*reset css */
#app {
  box-sizing: border-box;
  background-color: #fff;
  height: 100vh;
}
body {
  background: #fff;
}
body,
dl,
dd,
ul,
ol,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
form,
fieldset,
legend,
input,
textarea,
p,
thead,
tbody,
tfoot,
th,
td {
  margin: 0;
  padding: 0;
}
ul,
ol {
  list-style-type: none;
  list-style-image: none;
}
a {
  text-decoration: none;
}
a:active {
  background-color: transparent;
}
a:active,
a:hover {
  outline: 0 none;
}
a:focus {
  outline: 1px dotted;
}
html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  text-size-adjust: 100%;
  font-size: 62.5%;
}
body {
  font-size: 14px;
  line-height: 1.8;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 400;
}
h1 {
  font-size: 36px;
}
h2 {
  font-size: 30px;
}
h3 {
  font-size: 22px;
}
h4 {
  font-size: 18px;
}
h5 {
  font-size: 14px;
}
h6 {
  font-size: 12px;
}
a:link,
a:visited,
a:hover,
a:active {
  text-decoration: none;
}
a,
button {
  outline: none;
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
button,
input,
textarea {
  border: none;
  background: none;
  outline: none;
}
textarea {
  overflow: hidden;
}
a:hover {
  text-decoration: none;
}

/* common css */
.hide {
  display: none;
}
.left {
  float: left;
}
.right {
  float: right;
}
.clear {
  clear: both;
}
.clearfix {
  *zoom: 1;
}
.clearfix:before {
    display: table;
    line-height: 0;
    content: "";
}
.clearfix:after {
    display: table;
    line-height: 0;
    content: "";
    clear: both;
}
.line20 {
  height: 20px;
  background: #f9f9f9;
}
.line50 {
  height: 50px;
}
.line100 {
  height: 100px;
}
input::-webkit-search-cancel-button {
  display: none;
}
.barBox {
  background: #fff;
  border-bottom: 1vw solid #f8f8f8;
}
.paddingTop {
  padding-top: 38vw;
}
.paddingTop2 {
  padding-top: 16vw;
}
ul.infoNav {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  padding: 0 4.26vw;
  margin-top: -8vw;
}
ul.infoNav li {
    width: 49%;
    line-height: 8vw;
    height: 9vw;
    text-align: center;
    background: #f8f8f8;
    border-radius: 1.6vw 1.6vw 0 0;
    color: #007aff;
    font-size: 3.73vw;
}
ul.infoNav li span {
      border-radius: 1.6vw 1.6vw 0 0;
      background: #b2d7ff;
      display: block;
      height: 8vw;
}
ul.infoNav .active, ul.infoNav .on {
    background: #f8f8f8;
    color: #fff;
}
button.new {
  display: block;
  position: fixed;
  width: 12.8vw;
  height: 12.8vw;
  background: url(./assets/imgs/add.png) no-repeat;
  background-size: 100% 100%;
  bottom: 30vw;
  right: 5vw;
}
.mint-popup {
  width: 100vw;
}



